import { clsx, type ClassValue } from "clsx";
import { twMerge } from "tailwind-merge";

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs));
}

export function generateSlug(text: string, id?: string): string {
  // ุชุญููู ุงููุต ุงูุนุฑุจู ุฅูู ุฑุงุจุท ููุงุณุจ
  const arabicToEnglish: { [key: string]: string } = {
    'ุฃ': 'a', 'ุจ': 'b', 'ุช': 't', 'ุซ': 'th', 'ุฌ': 'j', 'ุญ': 'h', 'ุฎ': 'kh',
    'ุฏ': 'd', 'ุฐ': 'dh', 'ุฑ': 'r', 'ุฒ': 'z', 'ุณ': 's', 'ุด': 'sh', 'ุต': 's',
    'ุถ': 'd', 'ุท': 't', 'ุธ': 'z', 'ุน': 'a', 'ุบ': 'gh', 'ู': 'f', 'ู': 'q',
    'ู': 'k', 'ู': 'l', 'ู': 'm', 'ู': 'n', 'ู': 'h', 'ู': 'w', 'ู': 'y',
    'ุฉ': 'h', 'ู': 'a', 'ุก': 'a'
  };

  let slug = text
    .toLowerCase()
    .trim()
    // ุชุญููู ุงูุฃุฑูุงู ุงูุนุฑุจูุฉ ุฅูู ุฅูุฌููุฒูุฉ
    .replace(/[ู-ูฉ]/g, (d) => 'ููกูขูฃูคูฅูฆูงูจูฉ'.indexOf(d).toString())
    // ุชุญููู ุงูุญุฑูู ุงูุนุฑุจูุฉ
    .split('')
    .map(char => arabicToEnglish[char] || char)
    .join('')
    // ุฅุฒุงูุฉ ุงูุฑููุฒ ูุงููุณุงูุงุช
    .replace(/[^a-z0-9\s-]/g, '')
    .replace(/\s+/g, '-')
    .replace(/-+/g, '-')
    .replace(/^-|-$/g, '');

  // ุฅุถุงูุฉ ID ุฅุฐุง ูุงู ูุชููุฑุงู
  if (id) {
    slug = `${slug}-${id.substring(0, 8)}`;
  }

  return slug || 'project';
}